import { ReactElement } from "react"
import { Task } from "@rp/common/api-types"
import BrainStorming from "./assets/brainstorming.svg"
import Planning from "./assets/planning.svg"
import Research from "./assets/research.svg"
import Screening from "./assets/screening.svg"
import Writing from "./assets/writing.svg"

enum TaskId {
  Brainstorming = "brainstorming",
  Planning = "planning",
  Writing = "writing",
  Research = "research",
  Screening = "screening",
}

type TaskMapType = { id: TaskId | null; color: string; label: string }

export const getTaskMap = (task: typeof Task[number]): TaskMapType =>
  ({
    Brainstorming: {
      id: TaskId.Brainstorming,
      color: "#D9B600",
      label: "Brainstorming",
    },
    Planning: {
      id: TaskId.Planning,
      color: "#52C750",
      label: "Planning",
    },
    Writing: {
      id: TaskId.Writing,
      color: "#FE838B",
      label: "Writing",
    },
    Research: {
      id: TaskId.Research,
      color: "#AC8EFF",
      label: "Research",
    },
    Screening: {
      id: TaskId.Screening,
      color: "#68D6AE",
      label: "Screening",
    },
  }[task])

export const getTaskIcon = (taskId: TaskId | null): ReactElement => {
  switch (taskId) {
    case TaskId.Brainstorming:
      return <BrainStorming />
    case TaskId.Planning:
      return <Planning />
    case TaskId.Writing:
      return <Writing />
    case TaskId.Research:
      return <Research />
    case TaskId.Screening:
      return <Screening />
    default:
      return <div />
  }
}

export type { TaskMapType }
